  $cpod-elementary: #33CC66 !default;
  $cpod-preint: #FFCC00 !default;
  $cpod-intermediate: #FF6600 !default;
  $cpod-upperint: #CC0033 !default;
  $cpod-advanced: #990066 !default;
  $cpod-media: #003333 !default;

  .newbie {
    color: $cpod-newbie;
  }
