<template>
  <div>
    <div>
      <base-button v-if="access === 'premium' || access === 'admin'" class="float-right" size="sm" @click="toggleCharSet">{{charSet === 'simplified' ? 'Simp: 汉字' : 'Trad: 漢字'}}</base-button>
      <h2 class="h2">Expansion</h2>
    </div>
    <div v-if="access === 'premium' || access === 'admin'">
      <div v-for="expansion in expansionData" :key="expansion.phrase" class="offset-lg-2 col-lg-8">
        <h3 class="h3 example ml-2 mt-5">{{ expansion.phrase }}</h3>
        <dialogue-item
          v-for="(sentence, index) in expansion.examples"
          :key="index"
          :sentence-index="index"
          :cn="sentence.sentence"
          :en="sentence.target"
          :tab="`expansion-${sentence.id}`"
          :audioSource="`${audioRoot}/${sentence.audio}`"
          class="" />
      </div>
    </div>
    <div v-else>
      <upgrade/>
    </div>
  </div>
</template>

<script>
  import DialogueItem from "./DialogueItem";
  import Upgrade from "./Upgrade";

  export default {
    name: "Expansion",
    components: {Upgrade, DialogueItem},
    props: {
      lessonId: String,
      expansionData: Object | Array,
      hash_code: String,
      extra: Boolean
    },
    computed: {
      access() {
        return this.$store.state.userInfo ? this.$store.state.userInfo.access : 'free'
      },
      charSet() {
        return this.$store.state.userInfo ? this.$store.state.userInfo.charSet : 'simplified'
      },
      audioRoot() {
        return process.env.VUE_APP_ROOT_S3 + (this.extra ? '/extra/' : '/') +`${this.lessonId}/${this.hash_code}`
      }
    },
    methods: {
      async toggleCharSet () {
        await this.$store.dispatch('changeCharSet', this.charSet === 'simplified' ? 'traditional' : 'simplified')
      }
    },
  }
</script>

<style scoped>
  .example {
    font-size: 40px;
  }
</style>
