<template>
  <div class="">
    <base-button class="float-right" size="sm" @click="toggleCharSet">{{charSet === 'simplified' ? 'Simp: 汉字' : 'Trad: 漢字'}}</base-button>
<!--    <div class="float-right text-lg text-black-50">Pinyin: <base-switch @click="togglePinyin" onText="Hide" off-text="Show"></base-switch></div>-->
    <base-button class="float-right mr-2" :type="pinyin ? 'default' : 'secondary'" size="sm" @click="togglePinyin">{{ pinyin ? 'Hide Pinyin' : 'Show Pinyin' }}</base-button>
  </div>
</template>

<script>
import { mapState, mapMutations } from 'vuex'
import {
  TOGGLE_PINYIN
} from '@/mutation-types'

export default {
  name: "Settings",
  data() {
    return {
      show: false
    }
  },
  computed: {
    ...mapState({
      pinyin: state => state.userInfo.pinyin ? state.userInfo.pinyin : false,
      charSet: state => state.userInfo.charSet,
    })
  },
  methods: {
    async toggleCharSet () {
      await this.$store.dispatch('changeCharSet', this.charSet === 'simplified' ? 'traditional' : 'simplified')
    },
    ...mapMutations({
      togglePinyin: TOGGLE_PINYIN
    }),
  }
}
</script>

