<template>
  <div>
    <div>
      <settings />
      <h2 class="h2 pb-3">Exercises</h2>
    </div>
    <div id="exercises">
      <fade-transition :duration="200" origin="center top" mode="out-in">
        <div v-if="!hasFinished && !hasStarted && resultHistory.count > 0">
          <stats-card :class="passing ? 'bg-gradient-success' : 'bg-gradient-warning'">
            <div class="row">
              <div class="col">
                <h5 class="card-title text-uppercase text-muted mb-0 text-white">Previous Results</h5>
                <span class="h2 font-weight-bold mb-0 text-white">Taken: {{ resultHistory.count }} {{ resultHistory.count > 1 ? 'times' : 'time' }}</span>
              </div>
              <div class="col-auto">
                <div class="icon icon-shape bg-white text-dark rounded-circle shadow">
                  <i class="fa fa-star" :class="[passing ? 'golden' : '']"></i>
                </div>
              </div>
            </div>
            <p class="h3 mt-3 mb-0 text-sm">
              <span class="text-nowrap text-white">Last score: </span>
              <span class="text-white mr-2">{{ resultHistory.results[0].user_score }} / {{ resultHistory.results[0].total_score }}</span>
            </p>
            <p v-show="!passing" class="mb-0 text-sm">
              <span class="text-white">Aim for at least 70%. Try again!</span>
            </p>
          </stats-card>
        </div>
      </fade-transition>
      <exercise-view v-if="!hasFinished && !needsReload" @finished="toggleFinished" :key="lessonId"/>
      <Summary v-if="hasFinished" :lesson-id="lessonId" />
      <try-again v-if="hasFinished" @try-again="toggleFinished"/>
    </div>
  </div>
</template>

<script>

  import { mapState, mapMutations } from 'vuex'
  import shuffle from "lodash.shuffle"

  import ExerciseTypes from '@/helpers/ExerciseTypes'

  import Settings from "@/components/Exercises/Settings"
  import ExerciseView from "@/components/Exercises/ExerciseView"
  import Summary from "@/components/Exercises/Summary"
  import TryAgain from "@/components/Exercises/TryAgain"
  import FadeTransition from "vue2-transitions/src/Fade/FadeTransition";


  export default {
    name: "ExerciseModule",
    props: {
      exerciseData: Object,
      lessonId: String | Number
    },
    components: {
      FadeTransition,
      Settings,
      ExerciseView,
      Summary,
      TryAgain
    },
    computed: {
      passing() {return this.resultHistory.results[0].user_score / this.resultHistory.results[0].total_score > 0.7},
      ...mapState({
        exerciseLesson: state => state.exercises.lessonId,
        currentLesson: state => state.currentLesson.id,
        pinyin: state => state.userInfo.pinyin,
        charSet: state => state.userInfo.charSet,
        hasFinished: state => state.exercises.finished,
        hasStarted: state => state.exercises.history.length > 0
      })
    },
    data() {
      return {
        resultHistory: {},
        needsReload: true
      }
    },
    async beforeRouteUpdate (to, from, next) {
      if (this.currentLesson !== this.exerciseLesson) {
        this.$store.commit('exercises/RESET_EXERCISE');
      }
      this.needsReload = false;
      next();
    },
    mounted() {
      this.getHistory();
      if (this.exerciseData.choice && this.exerciseData.choice.length > 0){
        this.$store.commit('exercises/SET_MULTIPLE_CHOICE_QUESTION_DATA', shuffle(this.exerciseData.choice))
      } else {
        this.$store.commit('exercises/SET_MULTIPLE_CHOICE_QUESTION_DATA', []);
        this.setCurrentExerciseType(ExerciseTypes.getType(1));
      }
      if (this.exerciseData.matching && this.exerciseData.matching.length > 0){
        this.$store.commit('exercises/SET_MATCHING_QUESTION_DATA', shuffle(this.exerciseData.matching))
      } else {
        this.$store.commit('exercises/SET_MATCHING_QUESTION_DATA', []);
        if (this.exerciseData.choice.length === 0) {
          this.setCurrentExerciseType(ExerciseTypes.getType(2));
        }
      }
      if (this.exerciseData.rearrange && this.exerciseData.rearrange.length > 0){
        this.$store.commit('exercises/SET_SENTENCE_QUESTION_DATA', this.exerciseData.rearrange)
      } else {
        this.$store.commit('exercises/SET_SENTENCE_QUESTION_DATA', []);
        if (this.exerciseData.choice.length === 0 && this.exerciseData.matching.length === 0) {
          this.setCurrentExerciseType(ExerciseTypes.getType(3));
        }
      }
      if (this.exerciseData.audio && this.exerciseData.audio.length > 0){
        this.$store.commit('exercises/SET_DICTATION_QUESTION_DATA', shuffle(this.exerciseData.audio))
      } else {
        this.$store.commit('exercises/SET_DICTATION_QUESTION_DATA', []);
      }
      if (this.currentLesson !== this.exerciseLesson) {
        this.$store.commit('exercises/RESET_EXERCISE');
      }
      this.$store.commit('exercises/SET_LESSON_ID', this.lessonId);
      this.needsReload = false;
    },
    methods: {
      toggleFinished() {
        if(this.hasFinished) {
          this.getHistory();
        }
        this.$store.commit('exercises/TOGGLE_FINISHED');
      },
      async getHistory() {
        this.resultHistory = await this.axios.get(process.env.VUE_APP_ROOT_API + `/api/v1/exercises/results?lessonId=${this.lessonId}`)
          .then(({data}) => {return data})
          .catch(() => {return []});
      },
      ...mapMutations('exercises',{
        setCurrentExercise: 'CURRENT_EXERCISE',
        setCurrentExerciseType: 'CURRENT_EXERCISE_TYPE',
        resetAnswerHistory: 'RESET_ANSWER_HISTORY'
      })
    }
  }
</script>

<style scoped>
  .golden {
    color: gold;
  }
</style>
