<template>
  <router-view>
    <notifications></notifications>
  </router-view>
</template>

<script>
  import isNull from 'lodash/isNull'

  export default {
    name: 'app',

    async created () {
      if (!this.$store.state.token) {
        await this.$store.dispatch('getToken')
          .catch(() => window.location.href = '/login');
      } else {
        this.$store.commit('APPLY_TOKEN');
      }
      if (!this.$store.state.userInfo.access) {
        await this.$store.dispatch('getUserInfo')
          .catch(() => window.location.href = '/login');
      }

      document.addEventListener(
        'swUpdated', this.showRefreshUI, { once: true }
      );
      navigator.serviceWorker.addEventListener(
        'controllerchange', () => {
          if (this.refreshing) return;
          this.refreshing = true;
          window.location.reload();
        }
      );
    },
    computed: {
      userInfo: {
        get () {
          return this.$store.state.userInfo
        }
      }
    },
    methods: {
      showRefreshUI (e) {
        this.registration = e.detail;
        this.notify()
      },
      refreshApp () {
        if (!this.registration || !this.registration.waiting) { return; }
        this.registration.waiting.postMessage('skipWaiting');
      },
      notify() {
        this.$notify({type: 'primary', verticalAlign: 'bottom', horizontalAlign: 'center', message: 'New version available! Click here to update!', clickHandler: this.refreshApp() });
      },
    },
    data () {
      return {
        refreshing: false,
        registration: null,
      }
    }
  }
</script>
